"use client";
import SpaceshipAdminForm from "@/components/admin/SpaceshipAdminForm";
import Error from "@/components/Error";
import Loader from "@/components/Loader";
import Title from "@/components/ui/Title";
import { useSpacecraftQuery } from "@/hooks/queries/useSpacecraftQuery";
import React from "react";

const page = () => {
  const { data, isError, isLoading } = useSpacecraftQuery();

  function handleSubmit(formData: FormData) {
    if (!data) return;
    makeEditRequest(`http://localhost:4444/tours/admin/${data._id}`, "PUT", undefined, formData);
    setCurrentTour(null);
    setShowEditModal(false);
    refetch();
  }

  return (
    <>
      <link href="https://cdn.jsdelivr.net/npm/quill@2.0.3/dist/quill.snow.css" rel="stylesheet" />
      <Title className="text-4xl font-bold text-center mt-8" text="TURE" />
      <Title className="text-center mb-8" text="Her kan du redigere/slette eller lave nye ture" />
      {isError && !isLoading && <Error />}
      {isLoading && <Loader />}
      {data && (
        <SpaceshipAdminForm
          onSubmit={handleSubmit}
          defaultValues={{
            content: data.content,
            title: data.title,
          }}
        />
      )}
    </>
  );
};

export default page;
